import {dateDiff} from "../../../common/util.js"
export default {
	data() {
		return {
			tabs: [{"id":0,"title":"全部"},{"id":1,"title":"实事"},{"id":2,"title":"娱乐"},{"id":3,"title":"情感"},{"id":4,"title":"星座"},{"id":5,"title":"财经"}],
			content: [{"id":0,"type_id":0,"title":"不一样的北欧风，面积不大却很实用","thumb":"http://i2.tiimg.com/694610/cbd573a4f32869a7.jpg","content":"做一个优雅安静的女人，要懂得没有无缘无故的爱，也没有无缘无故的恨，要相信爱情，知道，付出不一定有收获，不付出，就一定没有，凡事尽力就好，绝不奢望奇迹出现！","time":"2019-12-13T11:00:09.000Z","head_img":"http://i2.tiimg.com/694610/81df69684288027f.jpg","author":"不动声色"},{"id":1,"type_id":0,"title":"心情烦躁的一天，该如何宣泄？","thumb":"https://upload-images.jianshu.io/upload_images/19536066-94140513dc6f46e4.jpg?imageMogr2/auto-orient/strip|imageView2/2/w/690/format/webp","content":"做一个优雅安静的女人，要懂得没有无缘无故的爱，也没有无缘无故的恨，要相信爱情，知道，付出不一定有收获，不付出，就一定没有，凡事尽力就好，绝不奢望奇迹出现！","time":"2019-12-13T11:00:09.000Z","head_img":"http://i2.tiimg.com/694610/81df69684288027f.jpg","author":"声色犬马"},{"id":2,"type_id":0,"title":"丢你老鼠健一生","thumb":"http://5b0988e595225.cdn.sohucs.com/q_70,c_zoom,w_640/images/20180811/5a6d4912cedc461bb601f6d663123146.webp","content":"嗨，在座的各位八百万勇士们，很高兴我们又见面了，我就是仰望高端玩家，毕竟DNF人才辈出，说话又好听，我超级喜欢这里。不过每天第一件事就是专注游戏，用心为你们带来最棒的体验。所以呢，我要给大家盘点旭旭宝宝的缺点和优点，一起看看吧。","time":"2019-12-13T11:00:09.000Z","head_img":"http://5b0988e595225.cdn.sohucs.com/q_70,c_zoom,w_640/images/20180811/5a6d4912cedc461bb601f6d663123146.webp","author":"朗格里格朗"}],
			defaultIndex: 0
		};
	},
	filters: {
		formDate(time) {
			return dateDiff(new Date(time).getTime());
		}
	},
	onLoad() {
		// uni.startPullDownRefresh();
		// this.initTabs(data => {
			// 默认第一个选项
			// this.getContent(data[0].id);
		// });
	},
	// onPullDownRefresh() {},
	methods: {
		// 获取标签页
		initTabs(callback) {
			const _this = this;
			_this.$api
				.tablist()
				.then(res => {
					if (+res.code === 1) {
						const { data } = res;
						_this.tabs = data;
						callback(data);
					}
				})
				.catch(res => {
					console.log(res);
				});
		},
		// 获取内容
		getContent(id) {
			const _this = this;
			_this.$api
				.tabForContent({ id: id })
				.then(res => {
					if (+res.code === 1) {
						const { data } = res;
						_this.content = data;
						uni.stopPullDownRefresh();
					}
				})
				.catch(res => {
					uni.stopPullDownRefresh();
					console.log(res);
				});
		},
		switchTab(id) {
			if (id === this.defaultIndex) {
				// 已点中的再次点击不处理
				return;
			}
			this.defaultIndex = id;
			this.getContent(id);
		}
	}
};
